.dropdown-menu {
    width: 200px;
    position: absolute;
    top: 30px;
    list-style: none;
    text-align: start;
    z-index: 99;
    
  }
  
  .dropdown-menu li {
    background: #3b3b3b;
    cursor: pointer;
  }
  
  .dropdown-menu li:hover {
    background: #5cabff;
    
  }
  
  .dropdown-menu.clicked {
    display: none;
  }
  
  .dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 5px;
    
  }
  
  @media screen and (max-width: 960px) {
    .fa-caret-down {
      display: none;
    }
  }